@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,400italic");

* {
  font-family: "Source Sans Pro", Helvetica, sans-serif;
  text-decoration: none;
  list-style: none;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: lightgray;
}
::-moz-placeholder { /* Firefox 19+ */
  color: lightgray;
}
:-ms-input-placeholder { /* IE 10+ */
  color: lightgray;
}
:-moz-placeholder { /* Firefox 18- */
  color: lightgray;
}

/* Hide scrollbar for Chrome, Safari and Opera */
  body::-webkit-scrollbar {
	  display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .example {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .body { 
    margin: auto; 
    max-width: 1800px; 
    align-items: center; 
    justify-content: center; 
    align-self: 'center'; 
    overflow: hidden;
  }

  .pdf {
    width: 100.5vw;
    height: 100%;
    align-self: center;
    z-index: 2;
    position: fixed;
  }

  .close-resume {
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 10px;
    padding-right: 10px;
    overflow: hidden;
    border-radius: 4px;
    background-color: black;
    color: white;
    position: fixed;
    font-size: small;
    top: 10px;
    left: 25px;
    z-index: 4;
    font-weight: 700;
    border-width: 0px;
  }

  .main-content {
    width: 100%;
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
    display: flex;
    transition: .3s ease-in-out;
  }


  /* side profile */

  .first-content{
    height: 100%;
    position: fixed;
    justify-content: center;
    display: flex;
    width: 35%;
    max-width: 490px;
    object-fit: cover;
    background-image: url('../src/images/woods.jpg');
    background-repeat: no-repeat;
    background-size: 100% 70rem;
    transition: .3s ease-in-out;
  }

  .header {
    flex-direction: column;
    height: 100%;
    display: flex;
    justify-content: space-around;
  }

  .inner {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    padding-right: 65px;
    padding-left: 65px;
    margin-top: 0px;
  }

  .image_avatar {
    width: 85px;
    height: 85px;
    border-radius: 50%;
    object-fit: cover;
    transition: .3s ease-in-out;
  }

  .intro {
    text-align: right;
    font-size: 14pt;
    color: rgba(255, 254, 255, 0.8);
    font-weight: 400;
		line-height: 1.75em;
  }

  .icon-container {
    justify-content: flex-end;
    display: flex;
    align-items: center;
    padding-right: 65px;
    padding-left: 65px;
    flex-direction: column;
    transition: .3s ease-in-out;
  }

  .icon-content {
    align-self: flex-end;
    display: flex;
    margin-bottom: 20px;
    padding-right: 2px;
    padding-left: 2px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition: .3s ease-in-out;
  }

  .icon-1 {
    margin: 4px;
    cursor: pointer;
  }

  .footer-details {
    align-self: flex-end;
    display: flex;
    color: rgba(255, 254, 255, 0.5);
    transition: .3s ease-in-out;
  }

  .footer-details, li {
    padding-right: 4px;
    padding-left: 4px;
  }

  .copyright {
    border-right: solid rgba(255, 254, 255, 0.5) 1px;
    font-size: 10px;
  }

  .author {
    font-size: 10px;
  }

  /* summary */

  .second-content{
    display: flex;
    justify-content: center;
    align-self: flex-end;
    align-items: center;
    width: 65vw;
    transition: .3s ease-in-out;
    max-width: 910px;
  }

  .contents {
    width: 88%;
    transition: .3s ease-in-out;
  }

  .summary-container {
    margin-top: 8.3rem;
    transition: .3s ease-in-out;
  }

  .summary-title {
    font-size: 24pt;
    color: rgba(0, 0, 0, 0.6);
  }

  .summary-content {
    color: rgba(0, 0, 0, 0.5);
    line-height: 1.80rem;
    width: 82%;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 4rem;
    border-bottom: solid 1.4px rgba(0, 0, 0, 0.08);
  }

  .description {
    margin-bottom: 25pt;
  }

  .action {
    border: solid rgba(0, 0, 0, 0.07) 2.5px;
    border-radius: 7px;
    width: 150px;
    height: 50px;
    padding: 10px;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.5);
    background-color: transparent;
    cursor: pointer;
    margin-bottom: 4rem;
    margin-left: -3px;
    transition: 0.3s ease-in;
  }

  .action:hover {
    color: black;
    border: solid rgba(0, 0, 0, .60) 2.5px;
    transition: 0.3s ease-in;
  }

  /* Recent work */

  .experience-container {
    color: rgba(0, 0, 0, 0.5);
    line-height: 1.80rem;
    width: 82%;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 4rem;
    border-bottom: solid 1.4px rgba(0, 0, 0, 0.08);
    transition: .3s ease-in-out;
  }

  .experience-title {
    font-size: 17pt;
    color: rgba(0, 0, 0, 0.6);
  }

  .work-items-row {
    display: flex; 
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: 115%;
    transition: .3s ease-in-out;
  }

  .work-item {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1rem;
    margin-right: 5rem;
    width: 38%;
    transition: .3s ease-in-out;
  }

  .thumbnail-container {
    display: flex;
    align-items: center;
    transition: .3s ease-in-out;
  }

  .overlay {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    aspect-ratio: 1.6;
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    width: 100%;
    cursor: pointer;
    opacity: 0;
  }

  .overlay:hover {
    opacity: 1;
    transition: 0.3s ease-in;
  }

  .overlay-btn {
    border: solid rgba(255, 255, 255, 0.5) 2px;
    border-radius: 5px;
    width: 85px;
    font-size: medium;
    padding: 5px;
    color: rgba(255, 255, 255, 0.5);
    background-color: transparent;
    cursor: pointer;
  }

  .thumbnail {
    justify-content: center;
    align-items: center;
    align-self: flex-start;
    border-radius: 10px;
    object-fit: cover;
    width: 100%;
    aspect-ratio: 1.6;
    transition: .3s ease-in-out;
  }

  .work-title {
    font-size: 17px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
  }

  .work-description {
    margin-top: -.80rem;
    font-size: 14px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
  }

  /* Send message content */
  .send-message-container {
    color: rgba(0, 0, 0, 0.5);
    line-height: 1.80rem;
    width: 82%;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 4rem;
    transition: .3s ease-in-out;
  }

  .send-message-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    transition: .3s ease-in-out;
  }

  .send-message-title {
    font-size: 17pt;
    color: rgba(0, 0, 0, 0.6);
  }

  .text-input-address {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  .text-input-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    transition: .3s ease-in-out;
  }

  .text-input-content {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
    transition: .3s ease-in-out;
  }

  .name-email {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }

  .name-container {
    display: flex;
    align-items: flex-end;
    width: 100%;
  }

  .email-container {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 100%;
  }

  input {
    width: 83%;
    border: none;
    font-size: medium;
    margin-bottom: 10px;
    padding: 15px;
    background-color: #EEEDEC;
    border-radius: 5px;
    transition: .3s ease-in-out;
  }

  .message{
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }

  #message {
    width: 100%;
    aspect-ratio: 2.2;
    font-size: medium;
    background-color: #EEEDEC;
    border-radius: 5px;
    padding: 8px;
    resize: none;
    border: none;
    transition: .3s ease-in-out;
  }

  .action-3 {
    border: solid rgba(0, 0, 0, 0.07) 2.5px;
    border-radius: 7px;
    width: 150px;
    height: 50px;
    padding: 10px;
    font-size: 16px;
    color: rgba(0, 0, 0, 0.5);
    background-color: transparent;
    cursor: pointer;
    margin-bottom: 4rem;
    margin-left: -2px;
    transition: 0.3s ease-in;
  }

  .action-3:hover {
    color: black;
    border: solid rgba(0, 0, 0, .60) 2.5px;
    transition: 0.3s ease-in;
  }

  .address-content {
    display: flex;
    justify-content: flex-start;
    padding: 5px;
  }

  .email-content {
    display: flex;
    justify-content: flex-start;
    padding: 5px;
    align-items: center;
  }

  .icon-2 {
    width: 25px;
    height: 25px;
    align-items: center;
    justify-content: center;
    display: flex;
  }

  .address, .email {
    margin-left: 4px;
    text-decoration: dotted;
  }

  .email {
    transition: 0.3s ease-in;
    color: rgba(0, 0, 0, 0.5);
    border-bottom: 1px dotted;
  }

  /* Technologies used */
  .tech-container {
    color: rgba(0, 0, 0, 0.5);
    line-height: 1.80rem;
    width: 82%;
    padding-bottom: 4rem;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 4rem;
    border-bottom: solid 1.4px rgba(0, 0, 0, 0.08);
    transition: .3s ease-in-out;
  }

  .tech-title {
    font-size: 17pt;
    color: rgba(0, 0, 0, 0.6);
  }

  #icons {
    font-size: 35px;
    padding: 7px;
  }

  .icons-row {
    display: flex;
    flex-wrap: wrap;
  }

  .expo-logo {
    width: 55px;
    height: 55px;
  }

  /* Footer */
  .footer {
    display: none;
    transition: .3s ease-in-out;
  }


  /* Modal container */
  .modal-container {
    z-index: 2;
    width: 100%;
    height: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    flex-direction: column;
    transition: .5s ease-in;
    justify-content: space-between;
    max-width: 1800px;
  }

  .modal-overlay {
    background-color: rgba(0, 0, 0, 0.5);
    transition: .3s ease-in-out;
    height: 100%;
    width: 100%;
    position: absolute;
  }

  .modal-content {
    width: 1450px;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .5s ease-in;
  }

  .modal-image-container {
    justify-content: flex-start;
    transition: .3s ease-in-out;
    flex-direction: column;
    align-items: center;
    display: flex;
    width: 50%;
    overflow: hidden;
    border-radius: 15px;
    z-index: 2;
    max-width: 720px;
  }

  .modal-image {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    height: 25rem;
    transition: .3s ease-in-out;
    object-fit: cover;
  }

  .modal-image-overlay{
    justify-content: space-between;
    transition: .9s ease-in-out;
    flex-direction: row;
    align-items: center;
    position: absolute;
    width: 100%;
    display: flex;
    height: 25rem;
    width: 100%;
    max-width: 720px;
  }

  .left-arrow {
    height: 100%;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: .3s ease-in-out;
  }

  .right-arrow {
    height: 100%;
    width: 200px;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    transition: .3s ease-in-out;
    justify-content: space-between;
  }

  .close {
    justify-content: flex-end;
    display: flex;
    padding: 10px;
    width: 100%;
  }

  #right, #left {
    cursor: pointer;
    padding-right: 25px;
    padding-left: 25px;
  }

  .modal-image-text-container {
    background-color: white;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
    transition: .3s ease-in-out;
  }

  .modal-image-text {
    padding: 15px;
    font-weight: 600;
    text-align: left;
    font-size: 13px;
    margin-bottom: 5px;
    color: rgba(0, 0, 0, 0.7);
    padding-left: 30px;
    padding-right: 30px;
  }

  .modal-image-button {
    border: solid rgba(0, 0, 0, 0.07) 1.5px;
    background-color: transparent;
    color: rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    justify-content: center;
    align-items: center;
    border-radius: 7px;
    margin-bottom: 8px;
    font-size: 16px;
    cursor: pointer;
    padding: 10px;
    display: flex;
    height: 15px;
    width: 70px;
  }

  .modal-image-button:hover {
    border: solid rgba(0, 0, 0, 0.50) 1.5px;
    transition: .3s ease-in-out;
  }


